	module.exports = {"data":{"markdownRemark":{"html":"<p><img src=\"https://forum.freecodecamp.com/images/emoji/emoji_one/triangular_flag_on_post.png?v=3\" title=\":triangular_flag_on_post:\" alt=\":triangular_flag_on_post:\" class=\"forum-image\"> Remember to use <a><strong><code>Read-Search-Ask</code></strong></a> if you get stuck. Try to pair program <img src=\"https://forum.freecodecamp.com/images/emoji/emoji_one/busts_in_silhouette.png?v=3\" title=\":busts_in_silhouette:\" alt=\":busts_in_silhouette:\" class=\"forum-image\"> and write your own code <img src=\"https://forum.freecodecamp.com/images/emoji/emoji_one/pencil.png?v=3\" title=\":pencil:\" alt=\":pencil:\" class=\"forum-image\"></p>\n<h3><img src=\"https://forum.freecodecamp.com/images/emoji/emoji_one/checkered_flag.png?v=3\" title=\":checkered_flag:\" alt=\":checkered_flag:\" class=\"forum-image\"> Problem Explanation:</h3>\n<p>The task is not that hard to understand, implementing it is the hardest part. You have a to validate a US phone number. This means there is a certain amount of numbers required, while you don’t need to put the country code, you will still need the area code and use one of the few formats allowed.</p>\n<h4>Relevant Links</h4>\n<ul>\n<li><a href='https://developer.mozilla.org/en-US/docs/Web/JavaScript/Reference/Global_Objects/RegExp' target='_blank' rel='nofollow'>RegExp</a></li>\n<li><a href='http://regexpal.com/' target='_blank' rel='nofollow'>regexpal.com</a></li>\n<li><a href='https://regex101.com/#javascript' target='_blank' rel='nofollow'>regex101.com/</a></li>\n</ul>\n<h2><img src=\"https://forum.freecodecamp.com/images/emoji/emoji_one/speech_balloon.png?v=3\" title=\":speech_balloon:\" alt=\":speech_balloon:\" class=\"forum-image\"> Hint: 1</h2>\n<p>There is no way around it, you will need to brush up your regular expressions skills.</p>\n<blockquote>\n<p><em>try to solve the problem now</em></p>\n</blockquote>\n<h2><img src=\"https://forum.freecodecamp.com/images/emoji/emoji_one/speech_balloon.png?v=3\" title=\":speech_balloon:\" alt=\":speech_balloon:\" class=\"forum-image\"> Hint: 2</h2>\n<p>Try using a site from the previous list to test the regex live while you create it.</p>\n<blockquote>\n<p><em>try to solve the problem now</em></p>\n</blockquote>\n<h2><img src=\"https://forum.freecodecamp.com/images/emoji/emoji_one/speech_balloon.png?v=3\" title=\":speech_balloon:\" alt=\":speech_balloon:\" class=\"forum-image\"> Hint: 3</h2>\n<p>Start by trying to get it to validate each format from the example, each one should take a new line, once you get to select them all, then add examples that should not be selected and make sure they are not selected.</p>\n<blockquote>\n<p><em>try to solve the problem now</em></p>\n</blockquote>\n<h2>Spoiler Alert!</h2>\n<p><img src=\"//discourse-user-assets.s3.amazonaws.com/original/2X/2/2d6c412a50797771301e7ceabd554cef4edcd74d.gif\" alt=\"warning sign\"></p>\n<p><strong>Solution ahead!</strong></p>\n<h2><img src=\"https://forum.freecodecamp.com/images/emoji/emoji_one/beginner.png?v=3\" title=\":beginner:\" alt=\":beginner:\" class=\"forum-image\"> Basic Code Solution:</h2>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-none\"><code>function telephoneCheck(str) {\n   var regex = /^(1\\s?)?(\\(\\d{3}\\)|\\d{3})<a href='https://forum.freecodecamp.com/images/emoji/emoji_one/rocket.png?v=3 \":rocket:\"' target='_blank' rel='nofollow'>\\s\\-]?\\d{3}[\\s\\-]?\\d{4}$/;\n   return regex.test(str);\n}\ntelephoneCheck(\"555-555-5555\");</code></pre>\n      </div>\n<p><img src=\"https://forum.freecodecamp.com/images/emoji/emoji_one/rocket.png?v=3\" title=\":rocket:\" alt=\":rocket:\" class=\"forum-image\"> <a href='https://repl.it/CLo9/0' target='_blank' rel='nofollow'>Run Code</a></p>\n<h3>Code Explanation:</h3>\n<ul>\n<li><code>^</code> denotes the beginning of the string <code>(1\\s?)?</code> checks allows for a “1” or a “1 ” at the beginning.</li>\n<li><code>\\d{n}</code> checks for exactly n number of digits so <code>(\\(\\d{3}\\)|\\d{3})</code> checks for three digits that are allowed to be between parenthesis.</li>\n<li><code>&#x3C;a href='https://forum.freecodecamp.com/images/emoji/emoji_one/sunflower.png?v=3 \":sunflower:\"' target='_blank' rel='nofollow'>\\s\\-]?</code> checks for spaces or dashes between the groups of digits.</li>\n<li><code>![:triangular_flag_on_post:](https://forum.freecodecamp.com/images/emoji/emoji_one/triangular_flag_on_post.png?v=3 \":triangular_flag_on_post:\") Remember to use &#x3C;a>**</code>Read-Search-Ask`**</a> if you get stuck. Try to pair program <img src=\"https://forum.freecodecamp.com/images/emoji/emoji_one/busts_in_silhouette.png?v=3\" title=\":busts_in_silhouette:\" alt=\":busts_in_silhouette:\" class=\"forum-image\"> and write your own code <img src=\"https://forum.freecodecamp.com/images/emoji/emoji_one/pencil.png?v=3\" title=\":pencil:\" alt=\":pencil:\" class=\"forum-image\"></li>\n</ul>\n<h3><img src=\"https://forum.freecodecamp.com/images/emoji/emoji_one/checkered_flag.png?v=3\" title=\":checkered_flag:\" alt=\":checkered_flag:\" class=\"forum-image\"> Problem Explanation:</h3>\n<p>The task is not that hard to understand, implementing it is the hardest part. You have a to validate a US phone number. This means there is a certain amount of numbers required, while you don’t need to put the country code, you will still need the area code and use one of the few formats allowed.</p>\n<h4>Relevant Links</h4>\n<ul>\n<li><a href='https://developer.mozilla.org/en-US/docs/Web/JavaScript/Reference/Global_Objects/RegExp' target='_blank' rel='nofollow'>RegExp</a></li>\n<li><a href='http://regexpal.com/' target='_blank' rel='nofollow'>regexpal.com</a></li>\n<li><a href='https://regex101.com/#javascript' target='_blank' rel='nofollow'>regex101.com/</a></li>\n</ul>\n<h2><img src=\"https://forum.freecodecamp.com/images/emoji/emoji_one/speech_balloon.png?v=3\" title=\":speech_balloon:\" alt=\":speech_balloon:\" class=\"forum-image\"> Hint: 1</h2>\n<p>There is no way around it, you will need to brush up your regular expressions skills.</p>\n<blockquote>\n<p><em>try to solve the problem now</em></p>\n</blockquote>\n<h2><img src=\"https://forum.freecodecamp.com/images/emoji/emoji_one/speech_balloon.png?v=3\" title=\":speech_balloon:\" alt=\":speech_balloon:\" class=\"forum-image\"> Hint: 2</h2>\n<p>Try using a site from the previous list to test the regex live while you create it.</p>\n<blockquote>\n<p><em>try to solve the problem now</em></p>\n</blockquote>\n<h2><img src=\"https://forum.freecodecamp.com/images/emoji/emoji_one/speech_balloon.png?v=3\" title=\":speech_balloon:\" alt=\":speech_balloon:\" class=\"forum-image\"> Hint: 3</h2>\n<p>Start by trying to get it to validate each format from the example, each one should take a new line, once you get to select them all, then add examples that should not be selected and make sure they are not selected.</p>\n<blockquote>\n<p><em>try to solve the problem now</em></p>\n</blockquote>\n<h2>Spoiler Alert!</h2>\n<p><img src=\"//discourse-user-assets.s3.amazonaws.com/original/2X/2/2d6c412a50797771301e7ceabd554cef4edcd74d.gif\" alt=\"warning sign\"></p>\n<p><strong>Solution ahead!</strong></p>\n<h2><img src=\"https://forum.freecodecamp.com/images/emoji/emoji_one/beginner.png?v=3\" title=\":beginner:\" alt=\":beginner:\" class=\"forum-image\"> Basic Code Solution:</h2>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-none\"><code>function telephoneCheck(str) {\n   var regex = /^(1\\s?)?(\\(\\d{3}\\)|\\d{3})<a href='https://forum.freecodecamp.com/images/emoji/emoji_one/rocket.png?v=3 \":rocket:\"' target='_blank' rel='nofollow'>\\s\\-]?\\d{3}[\\s\\-]?\\d{4}$/;\n   return regex.test(str);\n}\ntelephoneCheck(\"555-555-5555\");</code></pre>\n      </div>\n<p><img src=\"https://forum.freecodecamp.com/images/emoji/emoji_one/rocket.png?v=3\" title=\":rocket:\" alt=\":rocket:\" class=\"forum-image\"> <a href='https://repl.it/CLo9/0' target='_blank' rel='nofollow'>Run Code</a></p>\n<h3>Code Explanation:</h3>\n<ul>\n<li><code>^</code> denotes the beginning of the string <code>(1\\s?)?</code> checks allows for a “1” or a “1 ” at the beginning.</li>\n<li><code>\\d{n}</code> checks for exactly n number of digits so <code>(\\(\\d{3}\\)|\\d{3})</code> checks for three digits that are allowed to be between parenthesis.</li>\n<li>` denotes the end of the string. In this case the beginning and end of the string are used in the regex to prevent it from matching any longer string that might contain a valid phone number (eg. “s 555 555 5555 a”).</li>\n<li>Lastly we use <code>regex.test(str)</code> to test if the string adheres to the regular expression and return <code>true</code> or <code>false</code>.</li>\n</ul>\n<h4>Relevant Links</h4>\n<ul>\n<li><a>Regex Resources</a></li>\n</ul>\n<h2>![:sunflower:</a> Intermediate Code Solution:</h2>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-none\"><code>function telephoneCheck(str) {\n  var re = /^(?:(?:\\+?1\\s*(?:<a href='https://forum.freecodecamp.com/images/emoji/emoji_one/rocket.png?v=3 \":rocket:\"' target='_blank' rel='nofollow'>.-]\\s*)?)?(?:\\(\\s*([2-9]1[02-9]|[2-9][02-8]1|[2-9][02-8][02-9])\\s*\\)|([2-9]1[02-9]|[2-9][02-8]1|[2-9][02-8][02-9]))\\s*(?:[.-]\\s*)?)?([2-9]1[02-9]|[2-9][02-9]1|[2-9][02-9]{2})\\s*(?:[.-]\\s*)?([0-9]{4})$/;\n  return re.test(str);\n}\ntelephoneCheck(\"555-555-5555\");</code></pre>\n      </div>\n<p><img src=\"https://forum.freecodecamp.com/images/emoji/emoji_one/rocket.png?v=3\" title=\":rocket:\" alt=\":rocket:\" class=\"forum-image\"> <a href='https://repl.it/CLoa/0' target='_blank' rel='nofollow'>Run Code</a></p>\n<h3>Code Explanation:</h3>\n<p>This is an example of a very comprehensive and robust solution to validating US phone numbers client side. In such cases it might be much better and easier to implement this library <a href='https://github.com/googlei18n/libphonenumber' target='_blank' rel='nofollow'>libphonenumber</a>.</p>\n<h4>Relevant Links</h4>\n<ul>\n<li><a href='https://developer.mozilla.org/en-US/docs/Web/JavaScript/Reference/Global_Objects/RegExp/test' target='_blank' rel='nofollow'>Regex.test()</a></li>\n<li><a href='https://github.com/googlei18n/libphonenumber' target='_blank' rel='nofollow'>libphonenumber</a></li>\n</ul>\n<h2><img src=\"https://forum.freecodecamp.com/images/emoji/emoji_one/clipboard.png?v=3\" title=\":clipboard:\" alt=\":clipboard:\" class=\"forum-image\"> NOTES FOR CONTRIBUTIONS:</h2>\n<ul>\n<li><img src=\"https://forum.freecodecamp.com/images/emoji/emoji_one/warning.png?v=3\" title=\":warning:\" alt=\":warning:\" class=\"forum-image\"> <strong>DO NOT</strong> add solutions that are similar to any existing solutions. If you think it is <strong><em>similar but better</em></strong>, then try to merge (or replace) the existing similar solution.</li>\n<li>Add an explanation of your solution.</li>\n<li>Categorize the solution in one of the following categories — <strong>Basic</strong>, <strong>Intermediate</strong> and <strong>Advanced</strong>. <img src=\"https://forum.freecodecamp.com/images/emoji/emoji_one/traffic_light.png?v=3\" title=\":traffic_light:\" alt=\":traffic_light:\" class=\"forum-image\"></li>\n<li>Please add your username only if you have added any <strong>relevant main contents</strong>. (<img src=\"https://forum.freecodecamp.com/images/emoji/emoji_one/warning.png?v=3\" title=\":warning:\" alt=\":warning:\" class=\"forum-image\"> <strong><em>DO NOT</em></strong> <em>remove any existing usernames</em>)</li>\n</ul>\n<blockquote>\n<p>See <img src=\"https://forum.freecodecamp.com/images/emoji/emoji_one/point_right.png?v=3\" title=\":point_right:\" alt=\":point_right:\" class=\"forum-image\"> <a href='http://forum.freecodecamp.com/t/algorithm-article-template/14272' target='_blank' rel='nofollow'><strong><code>Wiki Challenge Solution Template</code></strong></a> for reference.</p>\n</blockquote>","fields":{"slug":"/certificates/telephone-number-validator/"},"frontmatter":{"title":"Telephone Number Validator"}}},"pathContext":{"slug":"/certificates/telephone-number-validator/"}}

